import React, {useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)
    const [countIsStarted, setCountIsStarted] = useState<boolean>(false)

    const start = () => {
        stop()
        setCountIsStarted(true)
        const id: number = window.setInterval(() => {
            setDate(new Date())
        }, 1000)
        setTimerId(id)
        //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)

    }

    const stop = () => {
        setCountIsStarted(false)
        clearTimeout(timerId)
        //    ,    (timerId <- undefined)
    }

    const onMouseEnter = () => {
        setShow(true)
       //     
    }
    const onMouseLeave = () => {
        setShow(false)
        //      
    }

    const stringTime = date && date.toLocaleTimeString('ru-Ru') || <br/>
    // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01)
    const stringDate = date && date.toLocaleDateString('ru-Ru') || <br/>
    // .. (01.02.2022) //  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = date && date.toLocaleDateString("en-US", { weekday: 'long' }) || <br/>
    const stringMonth = date && date.toLocaleDateString("en-US", { month: 'long' }) || <br/>

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={countIsStarted} //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={!countIsStarted} //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
